<template>
      <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ cartAmount }} <span v-if="cartAmount === 1">товар</span>
        <span v-else-if="cartAmount > 1 && cartAmount < 5">товара</span>
        <span v-else>товаров</span>
      </span>
    </div>

    <section v-if="cartsLoading" class="loader"></section>
    <section v-else class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link  tag="button" class="cart__button button button--primery" type="submit" :to="{name: 'order'}" v-show="cartLength">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapState } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
    components: {CartItem},
    filters: {
        numberFormat
    },
    computed: {
        ...mapGetters({products: 'CartDetailProducts', totalPrice: 'cartTotalPrice', cartAmount: 'cartTotalAmount'}),
        ...mapState(['cartsLoading']),
         cartLength () {
       return this.cartAmount > 0
      }
    },
    }

</script>