<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ this.$route.params.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                Картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderInfo.basket.items" :key="item.id">
              <h3> {{ item.product.title }}</h3>
              <b> {{ item.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ orderAmount }}</b> <span v-if="orderAmount === 1">товар</span>
                <span v-else-if="orderAmount > 1 && orderAmount < 5">товара</span>
                <span v-else>товаров</span> на сумму <b>{{ orderTotalPrice| numberFormat }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
import { mapState } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
   filters: {
        numberFormat
    },
    computed: {
        ...mapState(['orderInfo', 'orderInfo.basket.items']),
          orderAmount() {
      return this.orderInfo.basket.items.length
    },
    orderTotalPrice() {
            return this.orderInfo.basket.items.reduce((acc, item) => (item.price * item.quantity) + acc, 0);
        },
},
  /* methods: {
    orderAmount() {
      return this.orderInfo.basket.items.length
    }
  }, */
    created() {
    if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
        return;
    }


    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
}
}
</script>