<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page === 1 }"
        aria-label="Предыдущая страница" @click.prevent="firstPage()">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber of pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{ 'pagination__link--current': pageNumber === page }"
        @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow"
        :class="{ 'pagination__link--disabled': page === pages }" aria-label="Следующая страница"
        @click.prevent="nextPage()">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>

export default {
  model: {
    prop: 'page',
    event: 'paginate'
  },
  props: ['page', 'count', 'perPage'],
  watch: {
    page() {
      
    }
  },
  computed:
  {
    pages() {
      return Math.ceil(this.count / this.perPage);
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    firstPage() {
      if (this.page === 1) {
        this.page >= this.pages -1
      } else {this.$emit('paginate', (this.page - 1));}
    },
     nextPage() {
      if (this.page === this.pages) {
        this.page <= this.pages
      } else {this.$emit('paginate', (this.page + 1));}
    },
  }
}
</script>