<template>
  <main v-if="productLoading">Загрузка товара...</main>
  <main v-else-if="!productData">Не удалось загрузить товар</main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" >
            <b class="item__price">
               {{product.price | numberFormat}} ₽
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" value="blue" checked="">
                    <span class="colors__value" style="background-color: #73B6EA;">
                    </span>
                  </label>
                </li>
                <li class="colors__item">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" value="yellow">
                    <span class="colors__value" style="background-color: #FFBE15;">
                    </span>
                  </label>
                </li>
                <li class="colors__item">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" value="gray">
                    <span class="colors__value" style="background-color: #939393;">
                  </span></label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click.prevent="removeProducts()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">

                <button type="button" aria-label="Добавить один товар"  @click.prevent="addProducts()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <button class="button button--primery" type="submit" @click.prevent="addToCart" :disabled="productAddSend">
                В корзину
              </button>
            </div>
            <div v-show="productAdded">Товар добавлен в корзину</div>
             <div v-show="productAddSend">Добавляем товар в корзину...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>
  
          <a href="#">
            Все характеристики
          </a>
  
          <h3>Что это?</h3>
  
          <p>
           Wahoo ELEMNT BOLT GPS&nbsp;&mdash; это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их&nbsp;максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с&nbsp;датчиками по&nbsp;ANT+, объединяя полученную с&nbsp;них информацию. Данные отображаются на&nbsp;дисплее, а&nbsp;также сохраняются на&nbsp;смартфоне. При этом на&nbsp;мобильное устройство можно установить как фирменное приложение, так и&nbsp;различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с&nbsp;целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и&nbsp;планировать новые велопрогулки.
          </p>
  
          <h3>Дизайн</h3>
  
          <p>
           Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6&times;47,3&times;22,1&nbsp;мм. что не&nbsp;превышает габариты смартфона. Корпус гаджета выполнен из&nbsp;черного пластика. На&nbsp;обращенной к&nbsp;пользователю стороне расположен дисплей диагональю 56&nbsp;мм. На&nbsp;дисплей выводятся координаты и&nbsp;скорость, а&nbsp;также полученная со&nbsp;смартфона и&nbsp;синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и&nbsp;т.д. (датчики не&nbsp;входят в&nbsp;комплект поставки). Корпус велокомпьютера имеет степень защиты от&nbsp;влаги IPX7. Это означает, что устройство не&nbsp;боится пыли, а&nbsp;также выдерживает кратковременное (до&nbsp;30&nbsp;минут) погружение в&nbsp;воду на&nbsp;глубину не&nbsp;более 1&nbsp;метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
data(){
return {
  productAmount: 1,
  productData: null,
  productLoading: false,
  productLoadingFailed: false,

  productAdded: false,
  productAddSend: false,
}
},
computed: {
  product() {
     const product = this.productData;
      return {
        ...product,
        image: product.image.file.url,
      };
    },
  category() {
    return this.productData.category
  }
},
methods: {
    ...mapActions(['addProductToCart']),
  gotoPage,
  addToCart() {
    this.productAdded = false;
    this.productAddSend = true;
    this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
    .then(() => {
      this.productAdded = true;
      this.productAddSend = false;
    })
  },
  addProducts(){
    this.productAmount = this.productAmount + 1
  },
  removeProducts() {
    if (this.productAmount === 1) {
        this.productAmount
      } else {this.productAmount = this.productAmount - 1;}
  },
  loadProducts() {
    this.productLoading = true;
    this.productLoadingFailed = false;

    axios.get(API_BASE_URL + `/api/products/` + this.$route.params.id)

    .then(response => this.productData = response.data)
    .catch(() => this.productLoadingFailed = true)
    .then(() => this.productLoading = false);
  },
},

  watch: {
    '$route.params.id': {
      handler() {
        this.loadProducts();
      },
      immediate: true
    }
  },
filters: {
  numberFormat
  },
}
</script>