<template>
      <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>
                 {{ cartAmount }} 
            </b> <span v-if="cartAmount === 1">товар</span>
                <span v-else-if="cartAmount > 1 && cartAmount < 5">товара</span>
                <span v-else>товаров</span> на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
export default {
    filters: {numberFormat},
    computed: {
        ...mapGetters({products: 'CartDetailProducts', totalPrice: 'cartTotalPrice', cartAmount: 'cartTotalAmount'}),
},
}
</script>