<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filtercategoryId" :color-id.sync="filterColors"/>
  <section class="catalog">
    <div v-show="productsLoading" class="loader"></div>
    <div v-show="productsLoadingFailed">Произошла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще</button></div>
        <ProductList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
  </section>
    </div>
      
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';


export default {
  components: { ProductList, BasePagination, ProductFilter },
  
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filtercategoryId: 0,
      filterColors: 0,

      page: 1,
      productsPerPage: 3,
      productData: null,

      productsLoading: false,
      productsLoadingFailed: false,

    };
  },
  computed: {
    products () {
return this.productData 
    ? this.productData.items.map(product => {
      return {
        ...product,
        image: product.image.file.url
      }
    }) 
    : [];
    },
    countProducts () {
      return this.productData ? this.productData.pagination.total : 0;
    }
  },
  methods: {
  loadProducts() {
    this.productsLoading = true;
    this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {axios.get(API_BASE_URL + `/api/products`, {
        params: {
      page: this.page,
      limit: this.productsPerPage, 
      minPrice: this.filterPriceFrom,
      maxPrice: this.filterPriceTo,
      categoryId: this.filtercategoryId,
      colorId: this.filterColors,
        }
      })
      
      .then(response => this.productData = response.data)
       .catch(() => this.productsLoadingFailed = true)
      .then(() => this.productsLoading = false);
    }, 5000)
  }
  },
  created() {
    this.loadProducts();
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filtercategoryId() {
      this.loadProducts();
    },
     filterColors() {
      this.loadProducts();
    },
    
  }
}
</script>